.CRCheckbox label {padding-left: 31px; position: relative;font-weight: 400 !important; display: inline-block; padding: 0 0 0 2em; margin-right: 10px; height: 1.5em; line-height: 1.5; cursor: pointer; }
.CRCheckbox label::before,
.CRCheckbox label::after {position: absolute; top: 0; left: 0; display: block; width: 17px; height: 17px; }
.CRCheckbox label::before {content: " "; border: 1px solid #e5e5e5; border-radius: 3px; }
.CRCheckbox input[type="checkbox"]{position: absolute; opacity: 0; z-index: -1; margin: 0; }
.CRCheckbox input[type="checkbox"] + label::after {content: "\2714"; color: #FFF; line-height: 1.5; text-align: center; border: none; }
.CRCheckbox input[type="checkbox"]:checked + label::before {background:#1360fe; border-color:#1360fe ; }
.CRCheckbox input[type="checkbox"] + label::after{-webkit-transform: scale(0); transform: scale(0); }
.CRCheckbox input[type="checkbox"]:checked + label::after{-webkit-transform: scale(1); transform: scale(1); }

