/* ---------------------------------- */
/* User Menu
------------------------------------- */
.user-menu {
	position: relative;
	display: inline-block;
	cursor: pointer;
	vertical-align: top;
	margin-left: 100px;
}

.user-menu .user-name {
	transition: 0.2s;
	color: #444;
}

.user-menu .user-name {
	transition: 0.2s;
	white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 145px;
}

.user-menu .dropdown-toggle::after {
	display: inline-block;
    right: 0;
    position: absolute;
    top: 50%;
}

.user-menu.active .user-name:after,
.user-menu:hover .user-name:after,
.user-menu.active .user-name,
.user-menu:hover .user-name {
	color: #66676b;
}

.user-menu.active .user-name:after { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
.user-menu .dropdown-item:active {
    text-decoration: none;
	background-color:#cccccc;
	color: #000000;
}
/* Avatar */
.user-name span img {
	width: 100%;
	display: inline-block;
	border-radius: 50%;
	image-rendering: -webkit-optimize-contrast;
}

.user-name span {
	display: inline-block;
	width: 32px;
	height: 32px;
	margin-right: 10px;
	border-radius: 50%;
	position: absolute;
	left: -30px;
	top: 0px;
}

.user-menu ul {
	text-align: left;
	position: absolute;
	top: 45px;
	right: 0;
	list-style: none;
	background-color: #fff;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.12);
	border-radius: 4px;
	display: inline-block;
	width: 190px;
	font-size: 15px;
	-webkit-transform: translate3d(0,15px,0);
	        transform: translate3d(0,15px,0);
	padding: 16px 8px;
	box-sizing: border-box;
	transition: 0.25s;
	visibility: hidden;
	opacity: 0;
	z-index: 110;
}
.user-menu .dropdown-menu .dropdown-item{ padding: .25rem 1rem;}
.user-menu .dropdown-menu .dropdown-item a{
	line-height: 22px;
	display: inline-block;
	color: #696969;
	transition: 0.2s;
	width: 100%;
}

.user-menu ul li a:hover {
	color: #66676b;
}

.user-menu ul li a i {
	width: 20px;
	position: relative;
	display: inline-block;
}

.user-menu.active ul {
	visibility: visible;
	opacity: 1;
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
}
