/**====================================================================
  Plans & Pricing
====================================================================
***/
.plan-section{padding:3rem 0 7rem 0; position: relative;}
.plan-section .heading-title {width: 50%; margin: 0 auto 0rem auto; }

.plan-section .heading-title h2 {text-align: center; font-size: 25px; font-weight: 700; color: #091e42; margin: 0; padding-top: 0; }
.plan-section .heading-title p {font-size: 14px; color: #091e42; text-align: center; margin-bottom: 10px; }
.pricing-card {height: 100%; overflow: hidden; padding:0 20px; position: relative; border: 1px solid #eaebf6; border-radius: 2px; border-top: 2px solid #1360fe; }
.best-strip {background: #375288; text-align: center; color: #fff; font-weight: 600; font-size: 13px; padding: 3px 0; transform: rotate(50deg); position: absolute; width: 100%; top: 24px; right: -94px; }

.pricing-head {border-bottom: 1px solid #eaebf6; padding: 15px 0; margin-bottom: 30px; }
.pricing-head h2 {font-size: 18px; font-weight: 700; color: #091e42; margin: 0; padding: 0; text-align: center; }

.pricing-body ul {margin: 0; padding: 0; list-style: none; }
.pricing-body ul li{font-size: 14px; text-align: center; color: #77808a; }

.price-info {display: flex;     justify-content: center;align-items: center; position: relative; padding-top: 20px; padding-bottom: 20px; }
.price-value {font-size: 30px; font-weight: 700; margin: 0; padding: 0; color: #1d2127; }
.price-per {font-size: 12px; padding-left: 15px; color: #8c949e; font-weight: 600; }

.pricing-foot {padding: 15px 0px; text-align: center; } 
.btn-conversion {background: transparent; color: #1360fe; font-size: 14px; padding: 6px 25px; border-radius: 2px; font-weight: 600; border: 1px solid #1360fe; display: inline-block; }


.pricing-info {
    font-weight: bold;
    border-radius: 3px;
    cursor: default;
    display: inline-block;
    text-align: center;
    padding: 5px 15px;
    background: #1560fe;
    width: 100%;
    margin: 10px 0;
}span.pricing-value {
    font-size:25px;
    color: #fff;
    line-height: 28px;
}span.pricing-per-value {
    font-size: 12px;
    color: #fff;
    font-weight: 500;
}

.plan-pricing-info {
    background: #f4f7fd;
    position: relative;
    border: 1px solid #d2dce6;
    width: 30%;
    padding: 30px;
    margin: 0 auto;
    border-radius: 6px;
}
.plan-pricing-info p {
    font-size: 18px;
    color: #091e42;
    font-weight: 300;
        margin-bottom: 0;
}